.loader-item {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 4;
  background-color: #27386961;
}
.centered {
  position: fixed; /* or absolute */
  top: 30%;
  left: 50%;
}
.loader,
.loader:after {
  border-radius: 50%;
  width: 10em;
  height: 10em;
}
.loader {
  margin: 60px auto;
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  border-top: 1.1em solid rgba(60, 100, 196, 0.2);
  border-right: 1.1em solid rgba(60, 100, 196, 0.2);
  border-bottom: 1.1em solid rgba(60, 100, 196, 0.2);
  border-left: 1.1em solid #3c64c4;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
}
@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
