  margin-bottom: 1px;
  border-bottom-style: solid;
  border-bottom-width: 1px;
}
.top span, .leaf-front span {
  top: 0;
}
.leaf-front {
  position: absolute;
  z-index: 10;
}
.bottom, .leaf-back {
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
  border-top-style: solid;
  border-top-width: 1px;
}
.bottom span, .leaf-back span {
  bottom: 0;
}
.leaf-back {
  position: absolute;
