  background-size: cover;
  background-repeat: no-repeat;
  padding: 25px 30px;
  display: flex;
  flex-wrap: wrap;
}
.HELLBAR-WITHOUT-TIMER16 .cz_close_btn span {
  position: absolute;
  top: -2px;
  right: 30px;
  width: 30px;
