}
.HELLBAR-WITHOUT-TIMER16 .HELLBAR-WITHOUT-TIMER16-CTA:hover {
--x: 100%;
color: #ffffff;
}
.HELLBAR-WITHOUT-TIMER16 .hellobar_text p {
