.leadengagr-circular {
  -webkit-animation: leadengagr-rotate 2s linear infinite;
  animation: leadengagr-rotate 2s linear infinite;
  height: 100%;
  -webkit-transform-origin: center center;
  transform-origin: center center;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.leadengagr-circular .path {
  stroke-dasharray: 1, 200;
  stroke-dashoffset: 0;
  -webkit-animation: leadengagr-dash 1.5s ease-in-out infinite, color 6s ease-in-out infinite;
  animation: leadengagr-dash 1.5s ease-in-out infinite, color 6s ease-in-out infinite;
  stroke-linecap: round;
  stroke: #fff;
}
@-webkit-keyframes leadengagr-rotate {
  100% {
    -webkit-transform: leadengagr-rotate(360deg);
    transform: leadengagr-rotate(360deg);
  }
}
@keyframes leadengagr-rotate {
  100% {
    -webkit-transform: leadengagr-rotate(360deg);
    transform: leadengagr-rotate(360deg);
  }
}
@-webkit-keyframes leadengagr-dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35px;
  }
  100% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124px;
  }
}
@keyframes leadengagr-dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35px;
  }
  100% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124px;
  }
}
